import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the EditInformasiPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-informasi',
  templateUrl: 'edit-informasi.html',
})
export class EditInformasiPage {

  data;

  constructor(public navCtrl: NavController, public navParams: NavParams, private service: ServiceProvider) {
    this.data = navParams.get('data');
    console.log('edit data', this.data);
    if(!this.data) {
      this.navCtrl.setRoot('BerandaPage')
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditInformasiPage');
  }

  save() {
    console.log(this.data)
    this.service.saveData('Information', this.data).then((ok) => {
      console.log('updated', ok)
      this.navCtrl.pop();
    })
  }

}



